// TextField.stories.jsx
import React from 'react';
import TextField from '../Components/TextField/TextField';

export default {
    title: 'Components/TextField',
    component: TextField,
    argTypes: {
        label: { control: 'text' },
        variant: {
            control: { type: 'radio' },
            options: ['outlined', 'filled', 'standard']
        },
        size: {
            control: { type: 'radio' },
            options: ['normal', 'small']
        },
        margin: {
            control: { type: 'radio' },
            options: ['none', 'dense', 'normal']
        },
        disabled: { control: 'boolean' },
        required: { control: 'boolean' },
        autoFocus: { control: 'boolean' },
        name: { control: 'text' },
    },
};

const Template = (args) => <TextField {...args} />;

export const DefaultTextField = Template.bind({});
DefaultTextField.args = {
    label: 'Enter text',
    variant: 'outlined',
    size: 'normal',
    margin: 'none',
    disabled: false,
    required: false,
    autoFocus: false,
    name: 'textfield1',
};

export const SmallFilledTextField = Template.bind({});
SmallFilledTextField.args = {
    label: 'Small Filled',
    variant: 'filled',
    size: 'small',
    margin: 'dense',
    disabled: false,
    required: true,
    autoFocus: true,
    name: 'textfield2',
};

export const DisabledTextField = Template.bind({});
DisabledTextField.args = {
    label: 'Disabled',
    variant: 'standard',
    size: 'normal',
    margin: 'normal',
    disabled: true,
    required: false,
    autoFocus: false,
    name: 'textfield3',
};
